import React, {useState} from 'react'
import Form from './Form'
import SingleForm from './SingleForm'

import { Link, useNavigate } from 'react-router-dom'
import { useTranslation } from 'react-i18next'

const AddOrder = () => {

  const [alter, setAlter] = useState(false)


  const navigate = useNavigate();
  const { t } = useTranslation()
  return (
    <section className="main overflow-y-auto ml-[300px] z-0 top-0 w-[calc(100vw-300px)] pt-[30px] pb-[100px] right-0  pt-[30px]  mt-[70px] min-h-[calc(100vh-70px)] bg-white">

<h2 className='font-bold text-[#0e0e2b] text-[30px] mb-[30px] ml-[120px]'>{t("add-order")}   <button className='hover:bg-indigo-800 bg-indigo-500 text-white rounded px-[15px] w-[180px] py-[5px] font-medium text-[20px] mx-[30px]' onClick ={(() =>setAlter(!alter))}>Alter Form</button>
</h2>


    <div className="w-[40%]  ml-[100px] shadow-black/20 shadow-lg form  bg-white rounded-[10px]">
       {alter && <Form/>}
       {!alter && <SingleForm/>}
       
     
      </div>
      </section>
      
  )
}

export default AddOrder