import React, { useState, useEffect } from 'react';
import {  useNavigate } from "react-router-dom"
import { useTranslation } from 'react-i18next'

const OrderHistory = () => {
  const [orderData, setOrderData] = useState('')
  const [push, setPush] = useState(false)
  const [load, setLoad] = useState(false)
  const [sub, setSub] = useState('')
  const [search, setSearch] = useState('')
  const [sort, setSort] = useState(-1)
  const [skip, setSkip] =useState(0)
  const [totalActive, setTotalActive] =useState(0)
  const [totalComplete, setTotalComplete] =useState(0)
  const { t } = useTranslation()

  const navigate = useNavigate();


  useEffect(() => {
    fetch(`https://admin.entsorg-meister.de/api/read?search=${search}&sort=${sort}&skip=${skip}`)
    .then(response => response.json())
    .then((data) => setOrderData(data))
  },[load, push, search, sort, skip])



  useEffect(() => {
    fetch("https://admin.entsorg-meister.de/api/show")
    .then(response => response.json())
    .then((data) => setSub(data))
  },[])
 
  useEffect(() => {
    fetch("https://admin.entsorg-meister.de/api/totalactive")
    .then(response => response.json())
    .then((data) => setTotalActive(data.count))
  },[])

  useEffect(() => {
    fetch("https://admin.entsorg-meister.de/api/totalcomplete")
    .then(response => response.json())
    .then((data) => setTotalComplete(data.count))
  },[])
 

  const next = (id) => {
    localStorage.setItem('next_id', id)
    navigate('/next')
  }

  const edit = (id) => {
    localStorage.setItem('edit_id', id)
    navigate('/edit')
  }

  const sortOrder = (event) => {
    setSort(event.target.value)
  }

  async function onDelete(id) { 

    const response = await fetch('https://admin.entsorg-meister.de/api/remove', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
       id
      }),
    })

    const data = await response.json()

    if (data.status === 'ok') {
    
      setOrderData(orderData.filter((val)=>{
        return orderData._id!== id
      }))
      setPush(!push)
      setLoad(!load)

 
    }
  }
 
  return (
      <>
    

    <section className="main overflow-y-auto ml-[300px] z-0 top-0  pt-[30px] pb-[100px] right-0 mt-[70px] min-h-[calc(100vh-70px)] bg-white" >


    <div className="  ml-[100px] shadow-black/20 shadow-lg form  bg-white rounded-[10px] mb-[30px] mr-[]100px">

      </div>
      <h2 className='font-bold text-[#0e0e2b] text-[30px] mb-[30px] ml-[120px]'>Order History</h2>
      
      <div className='pr-[70px] min-w-[fit-content]'><div className="table min-w-[fit-content]  ml-[100px]  shadow-black/20 shadow-lg form  mb-[60px] bg-white rounded-[10px] ">
   
      <div className=' bg-indigo-500  mb-[20px] px-[20px] py-[10px] '>
  <input type="text" className='mr-[40px] w-[350px] px-[20px] py-[6px] rounded' placeholder={t('search-by')} value={search} onChange={(event) =>setSearch(event.target.value)}/>
<span className='pl-[100px] mr-[20px] text-white font-semibold'>{t('sort-by')} </span> 
<select className='bg-white text-indigo-700 font-semibold px-[20px] py-[6px] rounded' name="" id="" onChange={sortOrder}>
<option value="-1" selected>{t('newest')}</option>
<option value="1" >{t('oldest')}</option>

</select>
</div>
<div className="table-wrapper">
   <table className=' table '>
   <tr className='text-left w-[100%] bg-gradient-to-r from-[#6FA6FF] mr-3 to-indigo-500 text-white' >
   
     
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('id')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>No of revisions</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('name')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('postcode')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('area')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('email')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('phone')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('section')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('method')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('date')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('View')}</th>
     
     </tr>
     <tbody>
      {orderData!== '' &&
        orderData.map((order)=>(
          
           <tr className='text-left w-[100%] text-black border-[rgba(0,0,0,0.1) border-2'>
       
         
            {order.orderstatus==='incomplete'? <td className='font-semibold text-red-500 bg-red-100 w-[200px] px-[20px] py-[5px]'>Incomplete </td>: <td className='text-green-500 bg-green-100 font-semibold w-[200px] px-[20px] py-[5px]'>Complete</td>}
            {/* {/* <td className='font-semibold w-[200px] px-[20px] py-[5px]'>{order.name}</td> */}
           
          <td className='font-semibold w-[100%] px-[25px] py-[5px]'>    {order._id}</td>
          <td className='font-semibold w-[100%] px-[25px] py-[5px]'>    {order.city}</td>
          <td className='font-semibold w-[100%] px-[25px] py-[5px]'>    {order.postcode}</td>
          <td className='font-semibold w-[100%] px-[25px] py-[5px]'>    {order.sliderVal} m.sq</td>
          <td className='font-semibold w-[100%] px-[25px] py-[5px]'>    {order.email}</td> 
          <td className='font-semibold w-[100%s] px-[25px] py-[5px]'>    {order.contact}</td>
          <td className='font-semibold w-[100%] px-[25px] py-[5px]'>{order.section}</td>
          <td className='font-semibold w-[100%] px-[25px] py-[5px]'>{order.method}</td>
        
      

        
          {order.orderstatus==='complete'? <td className='font-semibold w-[100%] px-[20px] py-[5px]'>{
       new Date(order.date).getDate()+"/" + (new Date(order.date).getMonth() + 1) + "/" + new Date(order.date).getFullYear()
       }</td>:
       <td className='font-semibold w-[100%] px-[25px] py-[5px]'></td>}
       <td className='font-semibold w-[100%] px-[25px] py-[5px]'> 
       <button onClick={() =>edit(order._id)} className='w-[100%] px-[20px] hover:bg-orange-600 bg-orange-500 text-white rounded px-[25px] py-[6px] font-medium'>
       {t('View')}
         </button></td>

        
   



   
       </tr>

        ))}
      {orderData.length===0 && <tr className='text-left w-[100%] text-black border-[rgba(0,0,0,0.1)] border-2'>
        <p className=' p-[30px] font-bold text-[30px] text-blue-400'>{t('no-new-orders')} </p></tr> }
     
      {orderData=== '' && <tr className='text-left w-[100%] text-black border-[rgba(0,0,0,0.1)] border-2'>
      <p className=' p-[30px] font-bold text-[30px] text-blue-400'>{t('loading-datao')}<img className='inline-block' src="./images/spin.gif" alt="" /></p>
      
         </tr>}
     
     </tbody>
   </table>
   </div>
   </div>
      </div>
      
    <div className="  ml-[100px] shadow-black/20 shadow-lg form  bg-white rounded-[10px] mb-[30px]">


      </div>
      
      </section>
      </>
  )
}

export default OrderHistory