import React, {useState, useEffect} from 'react';
import { useTranslation } from 'react-i18next'


const ViewNewRequest = () => {
    const { t } = useTranslation()
const [name, setName] = useState('')
const [value, setValue] = useState('')
const [sliderVal, setSliderValue] = useState(0)
const [email, setEmail] = useState('')
const [contact, setContact] = useState('')
const [address, setAddress] = useState('')
const [section, setSection] = useState('')
const [others, setOthers] = useState('')
const [method, setMethod] = useState('')
const [date, setDate] = useState('')
const [floor, setFloor] = useState('')

const [postcode, setPostcode] = useState('')
const [city, setCity] = useState('')
const [description, setDescription] = useState('')
const [orderStatus, setOrderStatus] = useState('')

const [loading, setLoading] = useState(true)
const [loadingFile, setLoadingFile] = useState(true)
const [radius, setRadius] = useState(null)


 
 
  useEffect(() => {
    console.log(localStorage.getItem('viewNewRequestId'))
    fetch(`https://admin.entsorg-meister.de/api/viewNewReq?id=${localStorage.getItem('viewNewRequestId')}`)
    .then(response => response.json())
    .then((data) =>{
        console.log(data)
      setEmail(data.email)
        setName(data.name)
        setValue(data.value)
        setSliderValue(data.sliderVal)
        setEmail(data.email)
        setContact(data.contact)
        setSection(data.section)
        setAddress(data.address)
        setOthers(data.others)
        setMethod(data.method)
        setDate(data.date)
        setFloor(data.floor)
        setPostcode(data.postcode)
        setCity(data.city)
        setDescription(data.description)
        setOrderStatus(data.orderstatus)
      
        setLoading(false)

    })
    .catch((error)=> console.log(error))
  },[])

 


  return (
    <section className="main overflow-y-auto ml-[300px] z-0 top-0 w-[calc(100vw-300px)] pt-[30px] pb-[100px] right-0 mt-[70px] min-h-[calc(100vh-70px)] bg-white">

    <h2 className='font-bold text-[#0e0e2b] text-[30px] mb-[30px] ml-[120px]'>Request Info</h2>
   {loading && <p className='ml-[300px] p-[30px] font-bold text-[30px] text-blue-400'>{t('loading-data')} <img className='inline-block' src="./images/spin.gif" alt="" /></p>
}

{ !loading && <div className='bg-white w-[90%] h-[100%] p-[25px] shadow-lg m-[30px] border-2 border-grey ml-[120px]'>
   <div className='px-[20px] py-[10px]'>
    <label className='text-[#33333] font-bold ' htmlFor="">{t('name')} : </label>
    <span className=''>{name}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
    <label className='text-[#33333] font-bold ' htmlFor="">Volume : </label>
    <span className=''>{value}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
    <label className='text-[#33333] font-bold ' htmlFor="">Area : </label>
    <span className=''>{sliderVal} sq.m.</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
    <label className='text-[#33333] font-bold ' htmlFor="">Section : </label>
    <span className=''>{section} </span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
    <label className='text-[#33333] font-bold ' htmlFor="">{t('method')} : </label>
    <span className=''>{method}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
    <label className='text-[#33333] font-bold ' htmlFor="">{t('floor')} : </label>
    <span className=''>{floor}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
    <label className='text-[#33333] font-bold ' htmlFor="">Date of moving : </label>

    <span>{new Date(date).getDate()+"/" + (new Date(date).getMonth() + 1) + "/" + new Date(date).getFullYear()}</span>

   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
    <label className='text-[#33333] font-bold ' htmlFor="">Postcode : </label>
    <span className=''>{postcode}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
    <label className='text-[#33333] font-bold ' htmlFor="">City : </label>
    <span className=''>{city}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
    <label className='text-[#33333] font-bold ' htmlFor="">Address : </label>
    <span className=''>{address}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
    <label className='text-[#33333] font-bold ' htmlFor="">{t('phone')} : </label>
    <span className=''>{contact}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
    <label className='text-[#33333] font-bold ' htmlFor="">{t('email')} : </label>
    <span className=''>{email}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
    <label className='text-[#33333] font-bold ' htmlFor="">{t('description')} : </label>
    <span className=''>{description}</span>
   </div>
   <hr />

 
  
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold ' >Order Status : </label>
    <span>{orderStatus}</span>
   </div>
   
   </div>}

   {/* {(!loading && loadingFile) && <p className='ml-[300px] p-[30px] font-bold text-[30px] text-blue-400'>{t('loading-data')} <img className='inline-block' src="./images/spin.gif" alt="" /></p>}
   { (!loading && !loadingFile) && <div className='bg-white w-[90%] h-[100%] p-[25px] shadow-lg m-[30px] border-2 border-grey ml-[120px]'>
   <div className='px-[20px] py-[10px]'>
    <label className='text-[#33333] font-bold ' htmlFor="">{t('name')} : </label>
    <span className=''>Filw will be shown here</span>
   </div>
   </div>} */}


</section>
  )
}

export default ViewNewRequest

