import React, { useState, useEffect } from 'react';
import {  useNavigate } from "react-router-dom"
import { useTranslation } from 'react-i18next'

const SentToSubcontractor = () => {

    const goToTop = () => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth',
        });
      
        const interval = setTimeout(() => {
         setSuccess('')
         navigate("/sentoffers");
    
        }, 5000);
      
        return () => clearInterval(interval);
      
      };

  const [orderData, setOrderData] = useState('')
  const [push, setPush] = useState(false)
  const [load, setLoad] = useState(false)
  const [sub, setSub] = useState('')
  const [search, setSearch] = useState('')
  const [sort, setSort] = useState(-1)
  const [skip, setSkip] =useState(0)
  const [totalActive, setTotalActive] =useState(0)
  const [totalComplete, setTotalComplete] =useState(0)
  const [orderId, setOrderId] = useState('')
  const [reason, setReason] = useState('')
  const [showModal, setShowModal] = useState(false);
  const [success, setSuccess] = useState('')

  const { t } = useTranslation()

  const navigate = useNavigate();




  useEffect(() => {
    fetch(`https://admin.entsorg-meister.de/api/sentToSub?search=${search}&sort=${sort}&skip=${skip}`)
    .then(response => response.json())
    .then((data) => setOrderData(data))
  },[load, push, search, sort, skip])



  async function handleCancelSubmission(event) {
    setShowModal(false)
    let newDate =Date.now()
   const modifiedDate = new Date(newDate).getDate()+"/" + (new Date(newDate).getMonth() + 1) + "/" + new Date(newDate).getFullYear();

    const response = await fetch(
      "https://admin.entsorg-meister.de/api/cancelOrder",
      {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          id: orderId,
          text:`Cancelled from Sent Offers on ${modifiedDate} `,
          cancellationDate:modifiedDate,
          reason: reason
        }),
      }
    );

    const data = await response.json();

    if (data.status === 'ok') {
      goToTop()
      setSuccess("success")
    }
    else{
      goToTop()
      setSuccess("error")
    }
  }

  const next = (id) => {
    localStorage.setItem('next_id', id)
    navigate('/next')
  }

  
  const handleCancel = (id) => {
    setOrderId(id)
  setShowModal(true)
  }

  const handleManualPayments = (id) => {
    console.log(id)
    localStorage.setItem('manualPaymentId', id)
    navigate('/manualsubpayments')
  }
  
  const viewActive = (id) => {
    localStorage.setItem('sentOfferId', id)
    navigate('/view-sentoffer')
  }

  const revise = (id) => {
    localStorage.setItem('revisionId', id)
    navigate('/revisecost')
  }

 
  const edit = (id) => {
    localStorage.setItem('edit_id', id)
    navigate('/edit')
  }

  const sortOrder = (event) => {
    setSort(event.target.value)
  }

  async function onDelete(id) { 

    const response = await fetch('https://admin.entsorg-meister.de/api/remove', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
       id
      }),
    })

    const data = await response.json()

    if (data.status === 'ok') {
    
      setOrderData(orderData.filter((val)=>{
        return orderData._id!== id
      }))
      setPush(!push)
      setLoad(!load)

 
    }
  }
 
  return (
      <>
    

    <section className="main overflow-y-auto ml-[300px] z-0 top-0  pt-[30px] pb-[100px] right-0 mt-[70px] min-h-[calc(100vh-70px)] bg-white" >
 {showModal ?  <>
          <div
            className="justify-center items-center flex overflow-x-hidden overflow-y-auto fixed inset-0 z-50 outline-none focus:outline-none"
          >
            <div className="relative w-auto my-6 mx-auto max-w-3xl">
              {/*content*/}
              <div className="border-0 rounded-lg shadow-lg relative flex flex-col w-full bg-white outline-none focus:outline-none">
                {/*header*/}
                <div className="flex items-start justify-between p-5 border-b border-solid border-slate-200 rounded-t">
                  <h3 className="text-3xl font-semibold">
                    Cancel Orders
                  </h3>
                  <button
                    className="p-1 ml-auto bg-transparent border-0 text-black opacity-5 float-right text-3xl leading-none font-semibold outline-none focus:outline-none"
                    onClick={() => setShowModal(false)}
                  >
                    <span className="bg-transparent text-black opacity-5 h-6 w-6 text-2xl block outline-none focus:outline-none">
                      
                    </span>
                  </button>
                </div>
                {/*body*/}
                <div className="relative p-6 flex-auto">
                  <p className="my-4 text-slate-500 text-lg leading-relaxed">
                  Cancelling Order will put all the related information to this order into <span className='text-red-500 font-semibold' >Cancelled Offers</span> section. This action could not be undone.
                  <br></br>
                  <br></br>
                  An email regarding the same would be sent to the user.
                  </p>
                </div>
                <div class="mb-6 mx-[30px] ">
                <input 
                type="text"
                id="email" 
                onChange={(event) =>
                  setReason(event.target.value)
                }
                value={reason}
                class="bg-white border border-red-300 text-gray-900 text-sm rounded-lg focus:ring-red-800 focus:border-red-800 block w-full p-2.5 dark:bg-white dark:border-red-600 dark:placeholder-gray-400 dark:text-grey dark:focus:ring-red-800 dark:focus:border-red-500 border-red-500 border-2 text-grey" 
                placeholder="Reason for cancelling Order" 
                required ></input>
    </div> 
                {/*footer*/}
                <div className="flex items-center justify-end p-6 border-t border-solid border-slate-200 rounded-b">
                
                
                
                <button
                    className="text-red-500 background-transparent font-bold uppercase px-6 py-2 text-sm outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                    type="button"
                    onClick={() => setShowModal(false)}
                  >
                    Close
                  </button>
                 {reason.length <2 ? 
                 <button disabled
                    className="bg-red-800 text-[#999] active:bg-grey-800 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                    type="button"
                    
                  >
                    Confirm 
                  </button>
                  :
                  <button
                    className="bg-red-500 text-white active:bg-red-600 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                    type="submit"
                    onClick={handleCancelSubmission}
                  >
                    Confirm 
                  </button>}
                </div>
              </div>
            </div>
          </div>
          <div className="opacity-25 fixed inset-0 z-40 bg-black"></div>
        </>
      : null}
    

    <div className="  ml-[100px] shadow-black/20 shadow-lg form  bg-white rounded-[10px] mb-[30px] mr-[]100px">

      </div>
      <h2 className='font-bold text-[#0e0e2b] text-[30px] mb-[30px] ml-[120px]'>Sent to Subcontractor</h2>
      
      <div className='pr-[70px] min-w-[fit-content]'><div className="table min-w-[fit-content]  ml-[100px]  shadow-black/20 shadow-lg form  mb-[60px] bg-white rounded-[10px] ">
   
      <div className=' bg-indigo-500  mb-[20px] px-[20px] py-[10px] '>
  <input type="text" className='mr-[40px] w-[350px] px-[20px] py-[6px] rounded' placeholder={t('search-by')} value={search} onChange={(event) =>setSearch(event.target.value)}/>
<span className='pl-[100px] mr-[20px] text-white font-semibold'>{t('sort-by')} </span> 
<select className='bg-white text-indigo-700 font-semibold px-[20px] py-[6px] rounded' name="" id="" onChange={sortOrder}>
<option value="-1" selected>{t('newest')}</option>
<option value="1" >{t('oldest')}</option>

</select>
</div>
<div className="table-wrapper">
   <table className=' table '>
   <tr className='text-left w-[100%] bg-gradient-to-r from-[#6FA6FF] mr-3 to-indigo-500 text-white' >
   
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('name')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('id')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('city')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('postcode')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('area')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('email')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('phone')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('section')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('method')}</th>
     <th className='font-semibold w-[100%] px-[25px] py-[5px]'>{t('date')}</th>
     <th className='font-semibold w-[100%] px-[20px] py-[5px]'>{t('view')}</th>
     <th className='font-semibold w-[100%] px-[20px] py-[5px]'>{t('edit')}</th>
     <th className='font-semibold w-[100%] px-[20px] py-[5px]'>Revise Cost</th>
     <th className='font-semibold w-[100%] px-[20px] py-[5px]'>Manual Payments</th>
     <th className='font-semibold w-[100%] px-[20px] py-[5px]'>Cancel</th>
     <th className='font-semibold w-[100%] px-[20px] py-[5px]'>{t('delete')}</th>
     </tr>
     <tbody>
      {orderData!== '' &&
        orderData.map((order)=>(
          
           <tr className='text-left w-[100%] text-black border-[rgba(0,0,0,0.1) border-2'>
       
         
            {/* {order.orderstatus==='incomplete'? <td className='font-semibold text-red-500 bg-red-100 w-[200px] px-[20px] py-[5px]'>Incomplete </td>: <td className='text-green-500 bg-green-100 font-semibold w-[200px] px-[20px] py-[5px]'>Complete</td>} */}
            <td className='font-semibold w-[200px] px-[20px] py-[5px]'>{order.name}</td>
           
          <td className='font-semibold w-[100%] px-[25px] py-[5px]'>    {order._id}</td>
          <td className='font-semibold w-[100%] px-[25px] py-[5px]'>    {order.city}</td>
          <td className='font-semibold w-[100%] px-[25px] py-[5px]'>    {order.postcode}</td>
          <td className='font-semibold w-[100%] px-[25px] py-[5px]'>    {order.sliderVal} m.sq</td>
          <td className='font-semibold w-[100%] px-[25px] py-[5px]'>    {order.email}</td> 
          <td className='font-semibold w-[100%s] px-[25px] py-[5px]'>    {order.contact}</td>
          <td className='font-semibold w-[100%] px-[25px] py-[5px]'>{order.section}</td>
          <td className='font-semibold w-[100%] px-[25px] py-[5px]'>{order.method}</td>
        
      

        
          {order.orderstatus==='complete'? <td className='font-semibold w-[100%] px-[20px] py-[5px]'>{
       new Date(order.date).getDate()+"/" + (new Date(order.date).getMonth() + 1) + "/" + new Date(order.date).getFullYear()
       }</td>:
       <td className='font-semibold w-[100%] px-[25px] py-[5px]'></td>}
     <td className='font-semibold  px-[25px] py-[5px]'>  <button onClick={() =>viewActive(order._id)} className='hover:bg-orange-600 bg-orange-500 text-white rounded px-[15px] py-[5px] font-medium'>
       {t('view')}</button> </td> 
       <td className='font-semibold px-[25px] py-[5px]'> 
    <button onClick={() =>edit(order._id)} className='hover:bg-purple-600 bg-purple-500 text-white rounded px-[25px] py-[6px] font-medium'>
    {t('edit')}
      </button></td>
      <td className='font-semibold  px-[25px] py-[5px]'>   <button onClick={() =>revise(order._id)} className='hover:bg-green-600 bg-green-500 text-white rounded px-[25px] py-[6px] font-medium'>
    Revise Cost
      </button></td>
      <td className='font-semibold  px-[25px] py-[5px]'>       <button
       onClick={() => handleManualPayments(order._id)}
        className='hover:bg-yellow-600 bg-yellow-500 text-white rounded px-[25px] py-[6px] font-medium ease-linear transition-all duration-150'>
        Manual Payments
         </button></td>
       <td className='font-semibold  px-[25px] py-[5px]'> 
       <button
       onClick={() => handleCancel(order._id)}
        className='hover:bg-red-800 bg-[#800000] text-white rounded px-[25px] py-[6px] font-medium ease-linear transition-all duration-150'>
      Cancel
         </button></td>
       <td className='font-semibold px-[25px] py-[5px]'><button className='hover:bg-red-600 bg-red-500 text-white rounded px-[15px] py-[5px] font-medium' onClick ={(() => onDelete(order._id))}> {t('delete')}</button></td>
   
       </tr>

        ))}
      {orderData.length===0 && <tr className='text-left w-[100%] text-black border-[rgba(0,0,0,0.1)] border-2'>
        <p className=' p-[30px] font-bold text-[30px] text-blue-400'>{t('no-new-orders')} </p></tr> }
     
      {orderData=== '' && <tr className='text-left w-[100%] text-black border-[rgba(0,0,0,0.1)] border-2'>
      <p className=' p-[30px] font-bold text-[30px] text-blue-400'>{t('loading-datao')}<img className='inline-block' src="./images/spin.gif" alt="" /></p>
      
         </tr>}
     
     </tbody>
   </table>
   </div>
   </div>
      </div>
      
    <div className="  ml-[100px] shadow-black/20 shadow-lg form  bg-white rounded-[10px] mb-[30px]">


      </div>
      
      </section>
      </>
  )
}

export default SentToSubcontractor