import React, {useState, useEffect} from 'react'
import { useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next'

const ReviseCost = () => {
    
    const [cost, setCost] =useState(null)
    const [userper, setUserper] =useState(30)
    const [costRevisionLog, setCostRevisionLog] =useState('')
    const [id, setId] =useState('')
    const [revisionRemarks, setRevisionRemarks] =useState('')
    const [loading, setLoading] = useState(true)
  const [success, setSuccess] = useState('')
  const [showModal, setShowModal] = useState(false);


  const goToTop = () => {
    window.scrollTo({
        top: 0,
        behavior: 'smooth',
    });
  
    const interval = setTimeout(() => {
     setSuccess('')
     navigate("/sentoffers");
    
    }, 5000);
  
    return () => clearInterval(interval);
  
  };

  useEffect(() => {
    fetch(`https://admin.entsorg-meister.de/api/getCost?id=${localStorage.getItem('revisionId')}`)
    .then(response => response.json())
    .then((data) => {
        setCost(data.cost)
        setUserper(data.userper)
        const str =`Costing has been revised from Cost: ${data.cost} , User Advance Percent: ${data.userper} % to `
        setCostRevisionLog(str)
        setLoading(false)
    })
  },[])

  

    useEffect(() => {
      setId(localStorage.getItem('revisionId'))
    },[])

   
  

  const { t } = useTranslation()
  const navigate = useNavigate()


  const handleRevision = () => {
  setShowModal(true)
  }

  async function handleRevisionSubmission(event) {
    setShowModal(false)
    let newDate =Date.now()
   const modifiedDate = new Date(newDate).getDate()+"/" + (new Date(newDate).getMonth() + 1) + "/" + new Date(newDate).getFullYear();

   const response = await fetch("https://admin.entsorg-meister.de/api/costrevision", {
    method: 'PUT',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      id:id,
      cost:cost,
      userper:userper,
      revisionRemarks:revisionRemarks,
        revisionDate:modifiedDate,
        costRevisionLog: costRevisionLog + `Cost: ${cost} , User Advance Percent: ${userper} % on ${modifiedDate}`
    }),
}
);

    const data = await response.json();

    if (data.status === 'ok') {
      goToTop()
      setSuccess("success")
    }
    else{
      goToTop()
      setSuccess("error")
    }
  }

  async function handleCost(event) { 
    const response = await fetch("https://admin.entsorg-meister.de/api/cost", {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        id:localStorage.getItem('sendQuoteId'),
        cost:cost,
        userper:userper,
    
      }),
    })
  
    const data = await response.json()
  
    if (data.status === 'ok') {
      goToTop()
      setSuccess("success")
    }
    else{
      goToTop()
      setSuccess("error")
    }

    
  }
  

  return (
    <section className="main overflow-y-auto ml-[300px] z-0 top-0 w-[calc(100vw-300px)] pt-[30px] pb-[100px] right-0 mt-[70px] min-h-[calc(100vh-70px)] bg-white">
    
    {showModal ?  <>
          <div
            className="justify-center items-center flex overflow-x-hidden overflow-y-auto fixed inset-0 z-50 outline-none focus:outline-none"
          >
            <div className="relative w-auto my-6 mx-auto max-w-3xl">
              {/*content*/}
              <div className="border-0 rounded-lg shadow-lg relative flex flex-col w-full bg-white outline-none focus:outline-none">
                {/*header*/}
                <div className="flex items-start justify-between p-5 border-b border-solid border-slate-200 rounded-t">
                  <h3 className="text-3xl font-semibold">
                    Revise Cost
                  </h3>
                  <button
                    className="p-1 ml-auto bg-transparent border-0 text-black opacity-5 float-right text-3xl leading-none font-semibold outline-none focus:outline-none"
                    onClick={() => setShowModal(false)}
                  >
                    <span className="bg-transparent text-black opacity-5 h-6 w-6 text-2xl block outline-none focus:outline-none">
                      
                    </span>
                  </button>
                </div>
                {/*body*/}
                <div className="relative p-6 flex-auto">
                  <p className="my-4 text-slate-500 text-lg leading-relaxed">
                  Revising the cost or the user advance percentage would be notified to the user by an Email and a quote would be sent to user again. Only once the user has agreed, this order would be further proceeded.
                  <br></br>
                  <br></br>
                  </p>
                </div>
                <div class="mb-6 mx-[30px] ">
                <input 
                type="text"
                id="email" 
                onChange={(event) =>
                setRevisionRemarks(event.target.value)
                }
                value={revisionRemarks}
                class="bg-white border border-green-300 text-gray-900 text-sm rounded-lg focus:ring-red-800 focus:border-green-800 block w-full p-2.5 dark:bg-white dark:border-green-600 dark:placeholder-gray-400 dark:text-grey dark:focus:ring-green-800 dark:focus:border-green-500 border-green-500 border-2 text-grey" 
                placeholder="Put some extra remarks"
                required ></input>
    </div> 
                {/*footer*/}
                <div className="flex items-center justify-end p-6 border-t border-solid border-slate-200 rounded-b">
                
                
                
                <button
                    className="text-red-500 background-transparent font-bold uppercase px-6 py-2 text-sm outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                    type="button"
                    onClick={() => setShowModal(false)}
                  >
                    Close
                  </button>
                 {revisionRemarks.length <2 ? 
                 <button disabled
                    className="bg-green-800 text-[#999] active:bg-grey-800 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                    type="button"
                    
                  >
                    Confirm 
                  </button>
                  :
                  <button
                    className="bg-green-500 text-white active:bg-green-600 font-bold uppercase text-sm px-6 py-3 rounded shadow hover:shadow-lg outline-none focus:outline-none mr-1 mb-1 ease-linear transition-all duration-150"
                    type="submit"
                    onClick={handleRevisionSubmission}
                  >
                    Confirm 
                  </button>}
                </div>
              </div>
            </div>
          </div>
          <div className="opacity-25 fixed inset-0 z-40 bg-black"></div>
        </>
      : null}

      <h2 className='font-bold text-[#0e0e2b] text-[30px] mb-[1px] ml-[120px]'>Revise Cost</h2>
    <p className=' text-[#888] text-[12px] mb-[30px] ml-[120px]'>A confirmation email regarding the revision would be sent to the user's email address</p>


    {loading && <p className='ml-[300px] p-[30px] font-bold text-[30px] text-blue-400'>{t('loading-data')} <img className='inline-block' src="./images/spin.gif" alt="" /></p>
}

{ !loading &&
 <div className=" ml-[120px] w-[90vw] max-w-[800px] rounded text-[#333] bg-white   shadow-lg shadow-grey border-2 border-grey p-[45px]">




    <div class="parallel  justify-around items-center mb-[30px]">
      <form onSubmit={(e)=>{e.preventDefault()}}>
      {success==='success' && <div className='mb-[20px]'>
  <p className='bg-blue-100 border-[1px] border-solid border-blue-500 w-[100%] px-[30px] py-[10px] '>Quote sent successfully! You will be auto-redirected in a while</p>
</div>}
{success==='error' && <div className='mb-[20px]'>
  <p className='bg-red-100 border-[1px] border-solid border-red-500 w-[100%] px-[30px] py-[10px] '>An error occured. Please try again</p>
</div>}
      <div className="form-box mb-[10px] flex">
     <input className='bg-white px-[10px]  py-[5px]  shadow-lg border-2 border-grey' type="number" min={30} required placeholder ="Cost" 
     onChange={event => setCost(event.target.value)} value={cost} />
      <label  className='w-[280px] px-[10px]  py-[5px] text-white bg-blue-700'>{t('cost')} </label>
      </div>
      <div className="form-box mb-[10px] flex">
     
      <select className="bg-white px-[10px] w-[220px] py-[5px] shadow-lg border-2 border-grey" required name="userper" defaultValue={userper} onChange={event => setUserper(event.target.value)} >
                <option  value="20">20 %</option>
                <option value="25">25 %</option>
                <option value="30" >30 %</option>
                <option value="35">35 %</option>
            </select>
    <label  className='w-[280px] px-[10px]  py-[5px] text-white bg-blue-700'>{t('advance-cost')} % ({t('user')})</label>
      </div>

       </form>
      </div>
      {(cost<1 ) ?  <button disabled  className='px-[30px] mr-[30px] py-[8px] font-semibold text-[18px] text-white bg-green-800 text-grey'>{t('send')}</button>
      :
      <button
        onClick={() => handleRevision()} 
        className='px-[30px] mr-[30px] py-[8px] font-semibold text-[18px] text-white bg-green-500 hover:bg-green-700'>{t('send')}</button>}
    

</div>}

      </section>
  )
}

export default ReviseCost