import {useState, useEffect} from 'react';
import { useTranslation } from 'react-i18next'



const ViewSentOffer = () => {
  const { t } = useTranslation()
const [name, setName] = useState('')
const [postcode, setPostcode] = useState('')
const [phone, setPhone] = useState('')
const [email, setEmail] = useState('')
const [address, setAddress] = useState('')
const [sliderVal, setSliderVal] = useState()
const [section, setSection] = useState('')
const [method, setMethod] = useState('')
const [date, setDate] = useState('')
const [floor, setFloor] = useState('')
const [city, setCity] = useState('')
const [createdon, setCreatedon] = useState('')
const [description, setDescription] = useState('')
const [cost, setCost] = useState(null)
const [subcontractorper, setSubcontractorper] = useState(null)
const [userper, setUserper] = useState(null)
const [loading, setLoading] = useState(true)


  useEffect(() => {
    
    fetch(`https://admin.entsorg-meister.de/api/viewactive?id=${localStorage.getItem('viewActive_id')}`)
    .then(response => response.json())
    .then((data) =>{
      
        setName(data.name)
        setEmail(data.email)
        setPhone(data.contact)
        setPostcode(data.postcode)
        setSliderVal(data.sliderVal)
        setSection(data.section)
        setMethod(data.method)
        setFloor(data.floor)
        setDate(data.date)
        setCreatedon(data.createdon)
        setCity(data.city)
        setSubcontractorper(data.subper)
        setUserper(data.userper)
        setDescription(data.description)
        setAddress(data.address) 
        setCost(data.cost) 
        setLoading(false)

   
    })
    .catch((error)=> console.log(error))
  },[])
  
  return (
    <section className="main overflow-y-auto ml-[300px] z-0 top-0 w-[calc(100vw-300px)] pt-[30px] pb-[100px] right-0 mt-[70px] min-h-[calc(100vh-70px)] bg-white">



    <h2 className='font-bold text-[#0e0e2b] text-[30px] mb-[30px] ml-[120px]'>{t('order-info')}</h2>
    {loading && <p className='ml-[120px] p-[30px] font-bold text-[30px] text-blue-400'>{t('loading-data')} <img className='inline-block' src="./images/spin.gif" alt="" /></p>
}

{ !loading && 
<div className='bg-white w-[90%] h-[100%] p-[25px] shadow-lg m-[30px] border-2 border-grey ml-[120px]'>
   <div className='px-[20px] py-[10px]'>
    <label className='text-[#33333] font-bold ' htmlFor="">{t('name')} : </label>
    <span className=''>{name}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold ' >{t('phone')} : </label>
    <span>{phone}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold ' >{t('email')} : </label>
    <span>{email}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold ' >{t('postcode')} : </label>
    <span>{postcode}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold ' >{t('city')} : </label>
    <span>{city}</span>
   </div>
   <hr />
 
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold ' >{t('address')}  : </label>
    <span>{address}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold ' >{t('area')} : </label>
    <span>{sliderVal}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold ' >{t('section')} : </label>
    <span>{section}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold ' >{t('method')} : </label>
    <span>{method}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold ' >{t('floor')} : </label>
    <span>{floor}</span>
   </div>
   <hr />
  
 
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold ' >{t('order-created')} : </label>
    <span>{new Date(createdon).getDate()+"/" + (new Date(createdon).getMonth() + 1) + "/" + new Date(createdon).getFullYear()}</span>

   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold ' >{t('date-of-moving')} : </label>
    <span>{new Date(date).getDate()+"/" + (new Date(date).getMonth() + 1) + "/" + new Date(date).getFullYear()}</span>

   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold ' >{t('cost')} : </label>
    <span>{cost} </span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold ' >{t('user-ad')} : </label>
    <span>{userper} %</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold ' >{t('subcontractor-ad')} : </label>
    <span>{subcontractorper} %</span>
   </div>
   <hr />
   </div>}
</section>
  )
}

export default ViewSentOffer

