import React, {useState, useEffect} from 'react';
import { useTranslation } from 'react-i18next'



const ShowQuery = () => {
  const { t } = useTranslation()
const [name, setName] = useState('')
const [email, setEmail] = useState('')
const [phone, setPhone] = useState(null)
const [query, setQuery] = useState('')
const [loading, setLoading] = useState(true)


 
 
  useEffect(() => {
    
    fetch(`https://admin.entsorg-meister.de/api/showquery?id=${localStorage.getItem('query_id')}`)
    .then(response => response.json())
    .then((data) =>{
        console.log(data.email)
        setName(data.name)
     
        setEmail(data.email)
        setPhone(data.phone)
        setQuery(data.query)
        setLoading(false)
    })
    .catch((error)=> console.log(error))
  },[])

 


  return (
    <section className="main overflow-y-auto ml-[300px] z-0 top-0 w-[calc(100vw-300px)] pt-[30px] pb-[100px] right-0 mt-[70px] min-h-[calc(100vh-70px)] bg-white">

    <h2 className='font-bold text-[#0e0e2b] text-[30px] mb-[30px] ml-[120px]'>View Query</h2>
    {loading && <p className='ml-[300px] p-[30px] font-bold text-[30px] text-blue-400'>{t('loading-data')} <img className='inline-block' src="./images/spin.gif" alt="" /></p>
}
   {!loading && <div className='bg-white w-[90%] h-[100%] p-[25px] shadow-lg m-[30px] border-2 border-grey ml-[120px]'>
   <div className='px-[20px] py-[10px]'>




    <label className='text-[#33333] font-bold '>{t('name')} : </label>
    <span className=''>{name}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold '>{t('phone')} : </label>
    <span>{phone}</span>
   </div>
   <hr />
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold '>{t('email')} : </label>
    <span>{email}</span>
   </div>
  
   <hr />
   <div className='px-[20px] py-[10px]'>
   <label className='text-[#33333] font-bold '>{t('query')} : </label>
    <span>{query}</span>
   </div>

   
   <hr />
   </div> }
</section>
  )
}

export default ShowQuery

